@import url(https://fonts.googleapis.com/css?family=Calligraffitti|Indie+Flower|Merienda+One|Yeseva+One);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App-header {
  background-color: rgb(54, 53, 53);
  opacity: 0.95;
  min-height: 110px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
  text-align: center;
  margin-bottom: 5rem;
  border-bottom: 4px solid rgb(192, 86, 79);
  font-family: 'Merienda One', cursive;
  letter-spacing: 3px;
}

.ShoppingList {
  min-height: calc(100vh - 220px);
  padding-bottom: 50px;
  padding-top: 50px;
  margin-bottom: 80px;
  font-family: 'Indie Flower', cursive;
  font-size: 1.5em;
  background-color: #F3F7F8;
  opacity: 0.9;
  border: 2px solid rgba(146, 140, 136, 0.8);
  border-radius: 5px;
  box-shadow: 0 1px 1px rgba(56, 53, 50, 0.075) inset, 0 0 8px rgba(59, 56, 54, 0.6);
}

textarea:focus, input:focus, input[type]:focus, .uneditable-input:focus {
  border-color: rgba(119, 103, 92, 0.8);
  box-shadow: 0 1px 1px rgba(56, 53, 50, 0.075) inset, 0 0 8px rgba(59, 56, 54, 0.6);
  outline: 0 none;
}

